import React from 'react'
import '../../assests/pengzstack-iconfont/iconfont.css'
export function IconBold() {
    return (
        <span className="iconfont icon-bold " key="bold"></span>
    )
}

export function IconHeader() {
    return (
        <span className="iconfont icon-header " key="header"></span>
    )
}

export function IconQuote() {
    return (
        <span className="iconfont icon-quote " key="quote"></span>
    )
}

export function IconItalic() {
    return (
        <span className="iconfont icon-fontitalic" key="italic"></span>
    )
}

export function IconLink() {
    return (
        <span className="iconfont icon-link " key="link"></span>
    )
}

export function IconText() {
    return (
        <span className="iconfont icon-text " key="text"></span>
    )
}

export function IconCode() {
    return (
        <span className="iconfont icon-code " key="text"></span>
    )
}
