import axios from 'axios';
import React, { useState, useEffect, useRef } from 'react'
import { useHistory } from 'react-router';
import './index.scss'
import OAuthLogin from '../../components/OAuthLogin';
import WelcomeHeader from '../../components/WelcomeHeader';
import { Link } from 'react-router-dom';
import { ReactComponent as CloseIcon } from '../../assests/svgs/close.svg'
export default function LoginWithEmail(props: any) {
    const { setLogin } = props;
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [passwordMatched, setPasswordMatched] = useState<void | Boolean>(undefined)
    const emailFormat = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
    const passwordFormat = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/;

    const [fmtCheckEmail, setFmtCheckEmail] = useState(false);
    const [fmtCheckPassword, setFmtCheckPassword] = useState(false);


    const [emailExisted, setEmailExisted] = useState(null || Boolean);

    const emailRef = useRef<HTMLInputElement>(null), passwordRef = useRef<HTMLInputElement>(null)
    const history = useHistory();

    const handleChange = async (event: any) => {
        const { value } = event.target;
        switch (event.target.id) {
            case 'email':
                setEmail(value);
                break;
            case 'password1':
                setPassword(value);
                break;
            default:
                break;
        }
    }
    //useState Hooks are asynchronous, therefore we need to use useEffect Hook

    useEffect(() => setFmtCheckEmail(emailFormat.test(email)), [email])
    useEffect(() => setFmtCheckPassword(passwordFormat.test(password)), [password])

    const handleBlur = (event: any) => {
        const { value } = event.target
        switch (event.target.id) {
            case 'email':
                if (emailFormat) {
                    axios.get('/ajax/user-existed-check', { params: { email: value } }).then(res => {
                        setEmailExisted(res.data.status)
                    })
                }
                break;

            default:
                break;
        }
    }


    const handleLogin = () => {
        //only when all the formats are correct also username and email not existed can we create a new account
        if (fmtCheckEmail && fmtCheckPassword && emailExisted) {
            console.log('send request')
            axios.post('/ajax/login/email', {
                params: {
                    email, password
                }
            }).then(res => {

                const { status, token } = res.data
                if (status) {
                    setPasswordMatched(true)
                    history.push(`/redirect-page?status=true&token=${token}`)

                } else {
                    setPasswordMatched(false);
                    console.log('not matched')
                }

            })
        } else {
            if (!fmtCheckEmail) { emailRef.current?.focus(); return };
            if (!fmtCheckPassword) { passwordRef.current?.focus(); return };
        }
    }

    useEffect(() => {
        if (passwordMatched === false) {
            console.log('1213')
            passwordRef.current?.focus();
        }
    }, [passwordMatched])

    const handleClose = () => {
        setLogin(false);
    }
    return (
        <div className="login-with-email">
            <div className="aa login-container ">
                <CloseIcon className='icon-close' onClick={handleClose} />
                <WelcomeHeader />
                <OAuthLogin type="login" />
                <div className="login-input">
                    <section>
                        <input ref={emailRef} type="text" name="email" id="email" placeholder="Email" onChange={handleChange} onBlur={handleBlur} />
                        <div></div>
                        <span style={{ color: fmtCheckEmail && emailExisted ? 'green' : 'red' }}>{fmtCheckEmail ? emailExisted ? '√ Email address is valid' : '* Email address doesn\'t exists' : '* Email address not in correct format'}</span>
                    </section>
                    <section>
                        <input ref={passwordRef} type="password" name="password1" id="password1" placeholder="Password" onChange={handleChange} />
                        <span style={{ color: 'red' }}>
                            {fmtCheckPassword ?
                                (passwordMatched !== undefined ?
                                    (passwordMatched ? '' : '* password is not correct!')
                                    : '')
                                : '* Password must contain at least one alphabet and number.'}</span>
                    </section>
                    <button onClick={handleLogin}>Login</button>
                </div>
            </div>
        </div>
    )
}
