import React, { useState, useEffect } from 'react'
import { useLocation, useHistory } from 'react-router-dom';
import queryString from 'query-string'
import './index.scss'
import anime from 'animejs';
import Cat from '../../components/Cat';
export default function RedirectPage() {
    const [webStatus, setWebstatus] = useState<void | Boolean>(undefined)
    const pageMount = 0;
    const location = useLocation();
    const history = useHistory();
    const parsed = queryString.parse(location.search);
    const { action, status, token, msg } = parsed;

    useEffect(() => {

        const animation = anime
            ({
                targets: '.dot',
                translateY: -8,
                direction: 'alternate',
                loop: true,
                delay: function (el, i, l) {
                    return i * 30;
                },
                endDelay: function (el, i, l) {
                    return (l - i) * 30;
                }
            }
            )

        if (status === 'true' && typeof token == 'string') {
            localStorage.setItem('token', token);
            setWebstatus(true)
            setTimeout(() => { history.push('/home') }, 3000)
        } else {
            setWebstatus(false)
        }
    }, [pageMount])



    return (
        <div className="login-feedback">
            <div className=" ba clearfix">
                {webStatus === false ? <div className="redirect-msg">{`${action} error, ${msg}!`}</div> : <div className="redirect-msg">Login Successful! Redirecting
                    <div>
                        <div className="dot">.</div>
                        <div className="dot">.</div>
                        <div className="dot">.</div>
                        <div className="dot">.</div>
                    </div>

                </div>}
                <Cat />
            </div>
        </div >
    )
}
