import React from 'react'
import { BrowserRouter, Route, Redirect, Switch } from 'react-router-dom'
import Blog from '../../pages/Blog'
import BlogList from '../../pages/BlogList'
import Home from '../../pages/Home'
import WriteArticles from '../../pages/WriteArticle'
import SignupWithEmail from '../../pages/SignUpWithEmail'
import LoginWithEmail from '../../pages/LoginWithEmail'
import RedirectPage from '../../pages/RedirectPage'
import PublishFeedback from '../../pages/feedbackpages/PublishFeedback'
import NotFound from '../../pages/feedbackpages/NotFound'
import MyBlogs from '../../pages/MyBlogs'
import About from '../../pages/About'
import Contact from '../../pages/Contact'
import Tools from '../../pages/Tools'
import Home2 from '../../pages/Home2'
export default function RouterIndex() {
    return (
        <div className="pages">
            <Switch>
                <Route path="/home" component={Home2} />
                <Route path="/redirect-page" component={RedirectPage} />
                <Route path="/bloglist/blog" exact component={NotFound} />
                <Route path="/bloglist/blog" component={Blog} />
                <Route path="/publish-feedback" component={PublishFeedback} />
                <Route path='/myblogs' component={MyBlogs} />
                <Route path="/bloglist" component={BlogList} />
                <Route path="/drafts" component={WriteArticles} />
                <Route path='signupwithemail' component={SignupWithEmail} />
                <Route path='loginwithemail' component={LoginWithEmail} />
                <Route path='/aboutme' component={About} />
                <Route path='/contact' component={Contact} />
                <Route path='/tools' component={Tools} />
                <Redirect from="/" to="/home" exact />
                <Route path="*" component={NotFound} />
            </Switch>
        </div>
    )
}
