
import React, { useState } from 'react'
import './index.scss'
import { ReactComponent as InstagramBlackIcon } from '../../assests/svgs/instagram-black.svg';
import { ReactComponent as LinkedinBlackIcon } from '../../assests/svgs/linkedin-black.svg';
import { ReactComponent as FacebookBlackIcon } from '../../assests/svgs/facebook-black.svg';
import { ReactComponent as GithubBlackIcon } from '../../assests/svgs/github-black.svg';
import { ReactComponent as CodepenBlackIcon } from '../../assests/svgs/codepen-black.svg';
import { ReactComponent as Tentacles2 } from '../../assests/svgs/tentacles2.svg';
import { ReactComponent as Tentacles1 } from '../../assests/svgs/tentacles1.svg';
import { ReactComponent as Tentacles3 } from '../../assests/svgs/tentacles3.svg';
export default function About() {
    const [tentacleX, setTentacleX] = useState(0)
    const handleEnter = (e: any) => {
        const move = tentacleX + e.movementX;
        if (move > 820) {
            return
        }
        if (move < -370) {
            return
        }
        setTentacleX(move)
    }
    return (
        <div className="aboutme">
            <div className="aboutme-container clearfix">
                <div className="aboutme-mobile"><span>Visit by Desktop Device to Have Better Experience</span></div>
                <div className="aboutme-hoverme"><span>Hover Me</span><span className="aboutme-hoverme-down"></span></div>
                <div className="aboutme-contact">
                    <a href="https://www.instagram.com/zhangpeng42/" className="aboutme-contact-icon1"><InstagramBlackIcon />1</a>
                    <a href="https://www.facebook.com/tillian.roky/" className="aboutme-contact-icon2"><FacebookBlackIcon />2</a>
                    <a href="https://www.linkedin.com/in/peng-zhang-55a653113/" className="aboutme-contact-icon3"><LinkedinBlackIcon />3</a>
                    <a href="https://github.com/ZhangPeng4242" className="aboutme-contact-icon4"><GithubBlackIcon />4</a>
                    <a href="https://codepen.io/zhangpeng4242" className="aboutme-contact-icon5"><CodepenBlackIcon />5</a>
                </div>
                <div className="aboutme-profile1 clearfix">
                    <div className="aboutme-profile1-img">
                        <span className="aboutme-profile1-img-name1">Peng</span>
                        <span className="aboutme-profile1-img-name2">Zhang</span>
                        {/* <img className="aboutme-profile1-img1" src={process.env.PUBLIC_URL + '/assets/imgs/profile1.png'} alt="" /> */}
                        <img className="aboutme-profile1-img1-1" src={process.env.PUBLIC_URL + '/assets/imgs/profile1-4.png'} alt="" />
                        <img className="aboutme-profile1-img1-2" src={process.env.PUBLIC_URL + '/assets/imgs/profile1-3.png'} alt="" />
                        <img className="aboutme-profile1-img1-3" src={process.env.PUBLIC_URL + '/assets/imgs/profile1-2.png'} alt="" />
                        <span className="aboutme-profile1-img-text2">living in Tel Aviv, Israel</span>
                        <span className="aboutme-profile1-img-text1">25 years old</span>
                        <div className="aboutme-profile1-img-line1"></div>
                        <div className="aboutme-profile1-img-line2"></div>
                        {/* <img className="aboutme-profile1-img2" src={process.env.PUBLIC_URL + '/assets/imgs/profile1.png'} alt="" /> */}
                    </div>
                    <div className="aboutme-profile1-text">
                        <span className="aboutme-profile1-text1" >Hey there! My name is </span>
                        <span className="aboutme-profile1-text2" >Peng Zhang</span>
                        {/* <span className="aboutme-profile1-text3" >25 years old</span> */}
                        <span className="aboutme-profile1-text4">Self-taught junior full-stack / front-end developer and</span>
                        <div className="aboutme-profile1-text-container">
                            <span className="aboutme-profile1-text5">photographer</span>
                            <span className="aboutme-profile1-text6">video editor</span>
                            <span className="aboutme-profile1-text7">translator</span>
                            <span className="aboutme-profile1-text8">world traveler</span>
                            <span className="aboutme-profile1-text9">cosmopolite</span>
                            <span className="aboutme-profile1-text10">cat lover</span>
                        </div>

                    </div>

                </div>
                <div className="aboutme-profile2 clearfix">
                    <div className="aboutme-profile2-img">
                        <div className="aboutme-profile2-img-border1"></div>
                        <div className="aboutme-profile2-img-border2"></div>
                        <div className="aboutme-profile2-img-border3"></div>
                        <img className="aboutme-profile2-img1" src={process.env.PUBLIC_URL + '/assets/imgs/profile2.jpg'} alt="" />
                        <img className="aboutme-profile2-img3" src={process.env.PUBLIC_URL + '/assets/imgs/profile2-2.png'} alt="" />
                        <img className="aboutme-profile2-img2" src={process.env.PUBLIC_URL + '/assets/imgs/profile2-1.png'} alt="" />
                    </div>

                    <div className="aboutme-profile2-text">
                        <span className="aboutme-profile2-text1" >About My Skills </span>
                        <span className="aboutme-profile2-text2" >At the moment, I am a veteran of RTNM development, as you can see this website is built on </span>
                        <ul className="aboutme-profile2-text-list1"><li>React</li>
                            <li>TypeScript</li>
                            <li> Nodejs</li>
                            <li>MongoDB </li></ul>
                        <span className="aboutme-profile2-text4" > And of course, I am also very familiar with basics like  </span>
                        <ul className="aboutme-profile2-text-list2"><li>HTML5</li>
                            <li>CSS3(SASS)</li>
                            <li> JavaScript(ECMAScript6+)  </li>
                            <li>Python</li></ul>

                        <span className="aboutme-profile2-text6" >   as well as other technical stuff such as</span>
                        <ul className="aboutme-profile2-text-list3"><li>Redux</li>
                            <li>React Hooks</li>
                            <li>React-Virtualized</li>
                            <li>AJAX(Axios)</li>
                            <li>SQL(MySql)</li>
                            <li>Linux</li>
                            <li>Nginx</li>
                            <li>Git</li>
                            <li>Webpack</li></ul>
                        <span className="aboutme-profile2-text8" >  which are all part of the experience that I have learned from building this website.</span>
                        <span className="aboutme-profile2-text9" > As a new bee, however, I commit myself to keep learning and adapt myself to the fast-paced, changing environment.</span>
                        {/* <span className="aboutme-profile2-text10" >  I am currently learning Go, and also open lectures on Computer Networks, Data Structures & Algorithms from top-notch universities in order to catch up with some of the fundamental knowledge for a CS student. </span> */}
                    </div>



                </div>
                <div className="aboutme-profile3 clearfix" onMouseMove={handleEnter}>
                    <div className="aboutme-profile3-img">
                        <img className="aboutme-profile3-img1" src={process.env.PUBLIC_URL + '/assets/imgs/profile3.jpg'} alt="" />
                        <img className="aboutme-profile3-img2" src={process.env.PUBLIC_URL + '/assets/imgs/profile3-2.png'} alt="" />
                        <img className="aboutme-profile3-img3" src={process.env.PUBLIC_URL + '/assets/imgs/profile3-1.png'} alt="" />
                        <Tentacles1 className="aboutme-profile3-img-svg1" />
                        <Tentacles2 className="aboutme-profile3-img-svg2" />
                        <div className="aboutme-profile3-img-svgc" id="svgc" style={{ transform: `translateX(${tentacleX}px)` }}>
                            <Tentacles3 className="aboutme-profile3-img-svg3" /></div>
                    </div>
                    <div className="aboutme-profile3-text">
                        <div className="aboutme-profile3-text-container"> <span className="aboutme-profile3-text1" >I am originally from China, born in a city named Chengdu, the heart of Sichuan province. I always fascinate the cultures and languages from other parts of the world, and I particularly enjoy working in multinational and diverse environment.
                        </span>
                            <span className="aboutme-profile3-text2" >I speak native <b>Chinese</b> , fluent <b>English</b> (TOEFL113), upper intermediate <b>Spanish</b> (B2), and currently I am learning <b>Hebrew</b> as well. </span>
                            <span className="aboutme-profile3-text3" > Apart from this, I love traveling around, taking photos, making videos, doing things that I find beautiful and expressive.</span>
                            <span className="aboutme-profile3-text4" > Therefore, I am also exprienced with production tools like  <b>Premiere</b>, <b>After Effects</b>, <b>Tableau</b>, <b>Stata</b>, etc. </span>
                        </div>

                        <Tentacles1 className="aboutme-profile3-text-svg1" />
                    </div>
                </div>

                {/* <div className="aboutme-profile4"></div> */}
            </div>



        </div>

    )
}
