import React, { useEffect } from 'react'
import { useHistory } from 'react-router'
import Cat from '../../../components/Cat'
import './index.scss'
import anime from 'animejs';
export default function PublishFeedback() {
    const history = useHistory();


    useEffect(() => {

        const animation = anime
            ({
                targets: '.dot',
                translateY: -10,
                direction: 'alternate',
                loop: true,
                delay: function (el, i, l) {
                    return i * 30;
                },
                endDelay: function (el, i, l) {
                    return (l - i - 1) * 30;
                }
            }
            )
        setTimeout(() => {
            history.push('/home')
        }, 4000)
    })

    return (
        <div className="feedback clearfix">
            <div className=" ba clearfix">
                <div className="redirect-msg"><span>You have successfully published a new blog! Thank you for sharing! Redirecting to home page</span>
                    <div>
                        <div className="dot">.</div>
                        <div className="dot">.</div>
                        <div className="dot">.</div>
                        <div className="dot">.</div>
                    </div>

                </div>
                <Cat />
            </div>
        </div>
    )
}
