import React, { useEffect, useRef, useState, useLayoutEffect } from 'react'
import { useLocation } from 'react-router';
import './index.scss'
import Comments from '../../components/Comments'
import axios from 'axios';
import { TwitterShareButton, WhatsappShareButton, FacebookMessengerShareButton, FacebookShareButton, WhatsappIcon, WeiboShareButton, WeiboIcon, TwitterIcon, FacebookIcon, FacebookMessengerIcon } from 'react-share';
import { set } from 'animejs';
export default function Blog() {
    const share = useRef(null)
    const content = useRef(null)

    const pageMount = 0
    const [blog, setBlog] = useState({
        title: '',
        username: '',
        avatar_url: '',
        abstract: '',
        tags: [],
        publish_date: '',
        content: ''
    })
    const [loaded, setLoaded] = useState(false);
    const location = useLocation();

    useEffect(() => {
        const blog_id = location.pathname.slice(location.pathname.indexOf('&&') + 2);
        axios.get(`/ajax/getContent?blog_id=${blog_id}`).then((res) => {
            setBlog(res.data);
            setLoaded(true);
        })
    }, [pageMount])

    useEffect(() => {
        const content = document.getElementById('blogcontent')
        if (content) {
            content.innerHTML = blog.content;

        }
    }, [blog])

    useLayoutEffect(() => {
        if (loaded) {
            const caption = document.getElementsByTagName('figcaption');
            if (caption) {
                for (let i = 0; i < caption.length; i++) {
                    caption[i].contentEditable = false;
                }
            }
        }
    }, [loaded])

    return (
        <div className=" aa blog clearfix">
            <div className="ba blog-container">
                <div className="ca blog-header">
                    <section className="da blog-title">
                        <h1 className="daa g-blog-title">{blog.title} </h1 ></section>
                    {/* <span className="g-subtext">{blog.abstract}</span> */}
                    <section className="db blog-tags clearfix">
                        {blog.tags.map((tag, index) => {
                            return <span key={index} className="dba g-subtext">{tag}</span>
                        })}
                    </section>
                </div>
                <div className="cb blog-auther clearfix">
                    <img src={blog.avatar_url} alt="avatar" className="cba author-avatar" />
                    <div className="cbb">
                        <h4 className="cbba author-name g-subtext">{blog.username}</h4>
                        <h4 className="cbbb publish-date g-subtext">{new Date(blog.publish_date).toDateString().slice(3)}</h4>
                    </div>

                    <div className="cbc buttons">
                        <TwitterShareButton children={<TwitterIcon size={40} round={true} />} url={`Hey there! I have found this article on Pengzstack pretty useful: “${blog.title}” https://www.pengzstack.com${location.pathname}`} />
                        <WhatsappShareButton children={<WhatsappIcon size={40} round={true} />} url={`Hey there! I have found this article on Pengzstack pretty useful: “${blog.title}” https://www.pengzstack.com${location.pathname}`} />
                        <FacebookShareButton children={<FacebookIcon size={40} round={true} />} url={`https://www.pengzstack.com${location.pathname}`} />
                        {/* <FacebookMessengerShareButton children={<FacebookMessengerIcon bgStyle={{ "background-color": 'purple' }} size={48} round={true} />} url={`https://www.pengzstack.com${location.pathname}`} /> */}
                        <WeiboShareButton children={<WeiboIcon size={40} round={true} />} url={`Hey there! I have found this article on Pengzstack pretty useful: “${blog.title}” https://www.pengzstack.com${location.pathname}`} />

                    </div>

                </div>
                <div className="cc blog-content g-text" id="blogcontent" ref={content}></div>

                <Comments />
            </div>
        </div>
    )
}
