import React, { useState, useRef } from 'react'
import { IconBold, IconItalic, IconText, IconLink, IconHeader, IconQuote, IconCode } from '../../components/icons';
import './index.scss'

export default function EditBar() {

    const [link, setLink] = useState(false);
    const linkInput = useRef<HTMLInputElement>(null)
    // const handleKeydown = (e: any) => {
    //     if (e.keyCode === 13) {
    //         const linkFormat = /^[a-zA-Z0-9][-a-zA-Z0-9]{0,62}(\.[a-zA-Z0-9][-a-zA-Z0-9]{0,62})+(:[0-9]{1,5})?[-a-zA-Z0-9()@:%_\\\+\.~#?&//=]*$/
    //         if (linkFormat.test(e.target.value)) {
    //             setLink(e.target.value);
    //         } else {
    //             console.log('noo')
    //         }
    //     }
    // }

    const handleFocus = (e: any) => {
        e.preventDefault()
    }

    const handleUnfoldLink = (e: any) => {
        e.preventDefault();
        setLink(!link)
    }
    const handleInsert = (e: any) => {
        const linkFormat = /https?:\/\/(www\.)?[-a-zA-Z0-9@:%._\+~#=]{1,256}\.[a-zA-Z0-9()]{1,6}\b([-a-zA-Z0-9()@:%_\+.~#?&//=]*)/

        if (linkInput.current) {
            let hyperlink = linkInput.current.value;
            if (hyperlink.indexOf('hppt') !== 0) {
                hyperlink = 'https://' + hyperlink;
            }
            if (linkFormat.test(hyperlink)) {

                document.execCommand('createLink', false, hyperlink)
                linkInput.current.value = '';

            } else {
                e.preventDefault();
                linkInput.current.focus();
            }
        }
    }
    const handleCode = (e: any) => {
        // console.log(window.getSelection())
        // e.preventDefault();
        if (window.getSelection()?.anchorOffset === window.getSelection()?.focusOffset) {
            document.execCommand('insertHTML', false, `<code></code><p class="paragraph dba"></p>`);
            return;
        }
        document.execCommand('insertHTML', false, `<code>${window.getSelection()?.focusNode?.nodeValue}</code><p class="paragraph dba"></p>`);
    }
    return (
        <div>
            <div id="editBar" className="bb editBar" >
                <EditButton name="bold" cmd='bold' />
                <EditButton name="italic" cmd='italic' />
                {/* <button key="createLink"  ><IconLink /></button> */}
                <EditButton name="heading" cmd='formatBlock' arg='h3' />
                <EditButton name="text" cmd='formatBlock' arg='p' />
                <EditButton name="quote" cmd='formatBlock' arg='blockquote' />

                <button className="editButton bba hyperlink" onClick={handleUnfoldLink}><IconLink /></button>
                <button className="editButton bba code" onClick={handleCode}><IconCode /></button>
                <div className="editbar-link-input" style={{ transform: link ? 'scale(1)' : 'scale(0)' }}>
                    <input type="url" ref={linkInput} onFocus={handleFocus} className="input-link" placeholder="Link here..." />
                    <button onClick={handleInsert}>insert</button>
                </div>

                {/* <EditButton name="hyperlink" cmd='createLink' arg={link.indexOf('http') === -1 ? `http://${link}` : link} /> */}
            </div>
        </div>
    )
}



function EditButton(props: any) {

    const setIcon = (name: string) => {
        switch (name) {
            case 'bold':
                return <IconBold />;
            case 'italic':
                return <IconItalic />;
            case 'heading':
                return <IconHeader />;
            case 'text':
                return <IconText />;
            case 'quote':
                return <IconQuote />;

            default:
                return '';
        }
    }
    const editText = (event: any) => {
        event.preventDefault();
        document.execCommand(props.cmd, false, props.arg)
    }

    return (
        <button key={props.cmd} className={`editButton bba ${props.name}`} onMouseDown={editText} >{setIcon(props.name)}</button>
    )
}
