import React from 'react'
import './index.scss'
import anime from 'animejs'
import CatLogo from './CatLogo'
import { ReactComponent as ReactIcon } from './React.svg'
import { ReactComponent as NodejsIcon } from './Nodejs.svg'
export default function BlinkAnimation() {
    let i = 0;

    return (
        <div className='blink-animation-container clearfix' id="blinkAnimation" >
            {/* <span className="blink-anmiation"></span> */}
            <div className='blink-animation'>

                <span className={`blink-animation-${i++}`}>Welcome</span>
                <span className={`blink-animation-${i++}`}>to</span>
                <div className="together1">
                    <span className={`blink-animation-${i++}`}>Pengzstack!</span>
                    <span className={`blink-animation-${i++}`}><CatLogo /></span>
                </div>
                <span className={`blink-animation-${i++}`}>Are</span>
                <span className={`blink-animation-${i++}`}>you</span>
                <span className={`blink-animation-${i++}`}>Ready</span>
                <span className={`blink-animation-${i++}`}>to</span>
                <div className="together2">
                    <span className={`blink-animation-${i++}`}>Inspire</span>
                    <span className={`blink-animation-${i++}`}>Improve</span>
                </div>
                <span className={`blink-animation-${i++}`}>your</span>
                <span className={`blink-animation-${i++}`}>skills</span>
                <span className={`blink-animation-${i++}`}>in</span>
                <span className={`blink-animation-${i++}`}>{'<HTML/>'}</span>
                <span className={`blink-animation-${i++}`}>CSS</span>
                <span className={`blink-animation-${i++}`}>JavaScript</span>
                <span className={`blink-animation-${i++}`}>TypeScript</span>
                <span className={`blink-animation-${i++}`}>Redux</span>
                <span className={`blink-animation-${i++}`}>AJAX</span>
                <span className={`blink-animation-${i++}`}>React<ReactIcon className="react-icon" /></span>
                <span className={`blink-animation-${i++}`}>MongoDB</span>
                <span className={`blink-animation-${i++}`}>MySql</span>
                <span className={`blink-animation-${i++}`}>Linux</span>
                <span className={`blink-animation-${i++}`}>Nginx</span>
                <span className={`blink-animation-${i++}`}>Python</span>
                <span className={`blink-animation-${i++}`}><NodejsIcon className="nodejs-icon" /></span>
                <span className={`blink-animation-${i++}`}>This</span>
                <span className={`blink-animation-${i++}`}>is</span>
                <span className={`blink-animation-${i++}`}>Cool</span>
                <span className={`blink-animation-${i++}`}>Right?</span>
                <span className={`blink-animation-${i++}`}>Start browsing and writing!</span>
                <span className={`blink-animation-${i++}`}>Pengzstack!</span>
            </div>
        </div>
    )
}
