import React, { useEffect, useState, useCallback } from 'react'
import { useHistory, useLocation } from 'react-router'
import './index.scss'
import { List, AutoSizer } from 'react-virtualized'
import axios from 'axios'
import { Link } from 'react-router-dom'
import 'react-virtualized/styles.css'
import { loginCheck } from '../../functionutils';
import { ReactComponent as DeleteIcon } from '../../assests/svgs/delete.svg'
import { ReactComponent as EditIcon } from '../../assests/svgs/edit.svg'

const fmtdate = (date) => {
    return new Date(date).toDateString().slice(4);
}
const fmtstr = (title, blog_id) => {
    let str = title.replaceAll("?", "")
    str = str.replaceAll(" ", "-");
    str = str.toLowerCase();

    str = str + "&&" + blog_id;
    return str;
}

export default function MyBlogs() {
    const pageMount = 0
    const [list, setList] = useState([]);
    const [loggedin, setLoggedin] = useState();
    const [deleteBlog, setDeleteBlog] = useState(0);
    const history = useHistory();
    const location = useLocation();
    const token = localStorage.getItem('token')
    const [deleteNotice, setDeleteNotice] = useState(-1)
    const [deleting, setDeleting] = useState(false)
    useEffect(() => {
        const result = loginCheck();
        result ? setLoggedin(true) : setLoggedin(false);
    })
    useEffect(() => {
        if (loggedin === false) {
            history.push('/home')
        }
    }, [loggedin])

    useEffect(() => {
        if (token) {
            axios.get(`/ajax/bloglist/getmyblogs?token=${token}`).then(res => {
                const { status, blogListJSON } = res.data
                if (status) {
                    const blogList = JSON.parse(blogListJSON);
                    setList(blogList)
                }
            })
        }


    }, [deleteBlog])

    const handleDelete = (blog_id) => {
        return () => {
            setDeleting(true);
            axios.get(`/ajax/bloglist/delete-blog?token=${token}&&blog_id=${blog_id}`).then(res => {
                const { status } = res.data;
                if (status) {
                    setDeleteBlog(deleteBlog + 1);
                    setDeleteNotice(-1);
                    setDeleting(false);
                }
            })
        }
    }

    const handleEdit = (blog_id) => {
        return () => {
            axios.get(`/ajax/getContent?blog_id=${blog_id}`).then(res => {
                const { title, content } = res.data;
                sessionStorage.setItem('title', title);
                sessionStorage.setItem('content', content);
                history.push('/drafts');

            })
        }
    }
    const handleDeleteNotice = (index) => {
        return () => {
            setDeleteNotice(index);
        }
    }
    const handleCancel = () => {
        setDeleteNotice(-1);
    }
    const rowRenderer = ({ index, key, style }) => {
        return (
            <div key={key} className="blog-list-blog clearfix">
                <Link to={`/bloglist/blog/${fmtstr(list[index].title, list[index].blog_id)}`} >
                    <section className="g-title blog-list-blog-title"> <h1>{list[index].title}</h1></section>
                    <section className="blog-list-blog-img"> <img src={list[index].main_img_url} alt="" /></section>
                    <section className='g-subtext blog-list-blog-abstract'><span>{list[index].abstract}</span></section>
                </Link>
                <section className="blog-list-blog-footer">
                    <img src={list[index].avatar_url} alt="avatar" />
                    <span>&nbsp;{list[index].username}</span>
                    <span className="blog-list-blog-footer-date">&nbsp;published at {fmtdate(list[index].publish_date)}</span>
                    <span className="blog-list-blog-footer-tags">&nbsp;{list[index].tags.length > 0 ? list[index].tags[0] : 'BLOG'}</span>
                    <section className="blog-list-blog-edit">
                        <section className="blog-list-blog-edit-delete-msg" style={{ display: deleteNotice === index ? 'block' : 'none' }}>
                            <span className="blog-list-blog-edit-delete-msg1">Are you sure you want to delete this blog? It cannot be recovered once deleted.</span>
                            <span onClick={handleCancel} className="blog-list-blog-edit-delete-msg2">Cancel</span>
                            <span onClick={handleDelete(list[index].blog_id)} className="blog-list-blog-edit-delete-msg3">Delete</span>
                            <span className="blog-list-blog-edit-delete-msg4" style={{ display: deleting ? 'block' : 'none' }}> deleting...</span>
                        </section>
                        <div onClick={handleDeleteNotice(index)}>
                            <DeleteIcon className="icon-delete" /></div>
                        <div><EditIcon onClick={handleEdit(list[index].blog_id)} className="icon-edit" /></div>
                    </section>
                </section>
            </div>
        )
    }
    return (
        <div className="blog-list clearfix">
            <div className="aa blog-list-container clearfix">
                <div className="blog-list-header">
                    <span>My Blogs</span>
                    {/* <div>Fileter and search</div> */}
                </div>

                <div className="blog-list-blogs clearfix" style={{ height: document.body.clientWidth <= 800 ? `${395 * list.length}px` : `${630 * list.length}px` }}>
                    <AutoSizer>{({ height, width }) => (
                        <List autoHeight height={height} width={width} rowCount={list.length} rowHeight={document.body.clientWidth <= 800 ? 395 : 630} rowRenderer={rowRenderer} overscanRowCount={10} />
                    )}</AutoSizer>
                </div>
            </div>

        </div>
    )
}
