import axios from "axios"

import { useLayoutEffect, useRef } from "react";
import anime, { AnimeParams, AnimeInstance } from 'animejs';
//成功的登录状态是token 和session里面的值都有，并且session的时间戳没超过2小时
export function loginCheck() {
    const username = sessionStorage.getItem('username');
    const timeStamp: string | null | number = sessionStorage.getItem('timeStamp')
    const timeStampInt: number = parseInt(timeStamp ? timeStamp : '0');
    const now = new Date().getTime();
    const token = localStorage.getItem('token');

    if (token && username && Math.floor((now - timeStampInt) / 1000 / 60 / 60) <= 2) {
        //先找session 如果有的话 表明登录，直接获取就好
        console.log('session not expired')
        return true;
    } else {
        if (!token) {
            //没有session 没有token 表明没有登录， 跳转登录界面
            sessionStorage.clear();
            console.log('ssaasfd')
            return false;
        } else {
            //没有session，有token表明关闭过浏览器，需要重新验证token，验证成功后登录
            axios.get(`/ajax/login-check?token=${token}`).then(res => {
                const { status } = res.data;
                if (status) {
                    const { username, email, avatar_url, timeStamp } = res.data.user;
                    sessionStorage.setItem('username', username);
                    sessionStorage.setItem('email', email);
                    sessionStorage.setItem('avatar_url', avatar_url);
                    sessionStorage.setItem('timeStamp', timeStamp)
                    return true;
                } else {
                    //验证不成功，移除token，跳转登录界面
                    console.log('qwqewqe')
                    localStorage.removeItem('token')
                    return false;
                }
            })
        }
    }
}



export const useAnime = (props: AnimeParams) => {

    const animateTargetRef = useRef<any>(null);
    const animationRef = useRef<AnimeInstance>();

    useLayoutEffect(() => {
        if (!animateTargetRef.current) {
            console.log('please bind animation target ref');
            return;
        }

        animationRef.current = anime({
            ...props,
            targets: [animateTargetRef.current]
        })
    }, [props])
    return { animateTargetRef, animationRef }
}


export const fmtstr = (title: string, blog_id: string) => {
    let str = title.replaceAll("?", "")
    str = str.replaceAll(" ", "-");
    str = str.toLowerCase();
    // console.log(str)
    str = str + "&&" + blog_id;
    return str;
}