import React, { useState, useEffect, useRef } from 'react'
import './index.scss'
import { IconBold } from '../icons'
import { ReactComponent as PicInsert } from '../../assests/svgs/insertPic.svg'
import { ReactComponent as AddCode } from '../../assests/svgs/add-code.svg'
import { ReactComponent as Add } from '../../assests/svgs/add.svg'
import { nanoid } from 'nanoid'
import axios from 'axios'


export default function AddImgsBar() {
    const [imgsrc, setImgsrc] = useState({ src: '', alt: '' })
    const [gistCount, setGistCount] = useState(0)
    const codeUrl = useRef("");
    const codeRowCount = useRef(0)
    const gistUrl = useRef(null);
    const rowCount = useRef(null);
    const [oversize, setOversize] = useState(false)
    const [upload, setUpload] = useState(false)
    const [addBar, setAddBar] = useState(false)
    const [addCode, setAddCode] = useState(false)
    const handleUpload = () => {
        const img = document.getElementById('img');
        const imgfile = img.files[0];

        const token = localStorage.getItem('token')

        if (!imgfile) {
            console.log('please select an image!')
            return;
        }
        if (imgfile.size > 1248576) {
            console.log("picture oversize");
            setOversize(true);
            return;
        }
        setOversize(false);
        let param = new FormData();
        param.append('img', imgfile);
        setUpload(true);
        const config = {
            headers: {
                'Content-type': 'multipart/form-data'
            }
        }
        axios({
            method: 'post',
            url: `/ajax/imgupload?token=${token}`,
            data: param,
            headers: { "Content-Type": "multipart/form-data image/png" },
        }).then(res => {
            const { status, img_url, img_name } = res.data;
            if (status) {
                setImgsrc({ src: process.env.PUBLIC_URL + img_url, alt: img_name });
                setUpload(false);
            }
        })

    }
    useEffect(() => {
        if (imgsrc !== '') {
            const ic = document.getElementById('blogContent')
            const div = document.createElement('section')
            const imgs = document.createElement('img')
            imgs.src = imgsrc.src;
            imgs.alt = imgsrc.alt;
            div.appendChild(imgs);
            const figcap = document.createElement('figcaption');
            figcap.contentEditable = true
            const newimg = document.createElement('figure');
            div.contentEditable = false;
            newimg.appendChild(div);
            newimg.appendChild(figcap)
            ic.appendChild(newimg)
            const pchild = document.createElement('p');
            pchild.className = 'paragraph dba';
            ic.appendChild(pchild)
        }
    }, [imgsrc])


    useEffect(() => {
        const iframes = document.getElementsByTagName('iframe');
        if (iframes.length > 0) {
            const iframe = iframes[iframes.length - 1]
            console.log(iframe.children)
            console.log(iframe.clientHeight)
            // iframe.style.height = iframe.contentWindow.document.body.scrollHeight + 20 + 'px'

        }
    }, [gistCount])

    const handleAddcode = () => { }
    const handleInsertCode = () => {

        const urlFmtCheck = 'https://gist.github.com/';

        if (codeUrl.current.indexOf(urlFmtCheck) !== -1 && parseInt(codeRowCount.current) > 0) {
            const iframe = document.createElement('iframe')
            iframe.src = `data:text/html;charset=utf-8,<head><base target='_blank'/></head><body><script src='${codeUrl.current}'></script></body>`;
            iframe.sandbox = 'allow-same-origin allow-scripts';
            iframe.style.height = parseInt(codeRowCount.current) * 22.5 + 45 + 'px';
            iframe.scrolling = 'no'
            const ic = document.getElementById('blogContent')
            ic.append(iframe)
            const pchild = document.createElement('p');
            pchild.className = 'paragraph dba';
            ic.appendChild(pchild)
            setGistCount(gistCount + 1)
            codeUrl.current = '';
            codeRowCount.current = null;
            gistUrl.current.value = '';
            rowCount.current.value = '';
        } else {
            if (codeUrl.current.indexOf(urlFmtCheck) === -1) {
                gistUrl.current.focus();
                return;
            }
            if (!(parseInt(codeRowCount.current) > 0)) {
                rowCount.current.focus();
                return;
            }


        }
    }
    const handleChange1 = (event) => {
        codeUrl.current = event.target.value;
    }
    const handleChange2 = (event) => {
        codeRowCount.current = event.target.value;
    }
    const unfoldAddBar = () => {
        setAddBar(!addBar)
        setAddCode(false)
        console.log(111)
    }
    const unfoldInsertCode = () => {
        setAddCode(!addCode)
    }
    return (<div className="blog-add">
        <div className="blog-add-err" style={{ display: oversize ? "block" : "none" }}>
            <span className="blog-add-err-msg" >* Maximum image size 1mb</span>
        </div>
        <div className="blog-add-info" style={{ display: upload ? "block" : "none" }}>
            <span className="blog-add-info-msg2" >Uploading...</span>
        </div>
        <div className="blog-add-icon" style={{ transform: addBar ? 'rotate(90deg)' : 'rotate(-90deg)' }} onClick={unfoldAddBar} ><Add className="icon-add" /></div>
        <div className="blog-add-buttons" style={{ width: addBar ? '12rem' : '0rem' }}>
            <div className="blog-add-img">
                <span><PicInsert className="icon-insertPic" /></span>
                <input placeholder="hh" type="file" id="img" accept="image/png, image/jepg, image/jpg" onChange={handleUpload} />
            </div>
            <div className="blog-add-code" >
                <span className="addcode"><AddCode className="icon-addcode" onClick={unfoldInsertCode} /></span>
                <div style={{ display: addCode ? 'block' : 'none' }} >
                    <span onClick={handleAddcode}></span>
                    <input ref={gistUrl} type="text" placeholder="Github gist embedded url...(.js)" onChange={handleChange1} />
                    <input ref={rowCount} type="text" placeholder="Code row count" onChange={handleChange2} />
                    <button onClick={handleInsertCode}>insert</button>
                </div>
            </div>
        </div>


    </div >
    )
}
