import React, { useState, useEffect, useRef } from 'react'
import './index.scss'
import ContentEditable from 'react-contenteditable';
import EditBar from '../../components/EditBars';
import AddImgsBar from '../../components/AddImgsBar';

import { loginCheck } from '../../functionutils';
import { useHistory } from 'react-router';
import PublishBlog from '../../components/PublishBlog';


export default function WriteArticles() {

    const [loggedin, setLoggedin] = useState<void | Boolean>(undefined);
    const [publish, setPublish] = useState(false)
    const history = useHistory();
    const text = useRef("");
    // const title = useRef("")

    useEffect(() => {
        const result = loginCheck();
        result ? setLoggedin(true) : setLoggedin(false);
    })

    useEffect(() => {
        if (loggedin === false) {
            history.replace('/home')
        }
    }, [loggedin])


    let count = 1;
    const initContent = () => {
        const draft = sessionStorage.getItem('content');
        if (draft) {
            return draft
        }
        return `<p class='paragraph dba' ></p>`
    }

    const initTitle = () => {
        const draft = sessionStorage.getItem('title');
        if (draft) {
            return `<h1 class='blog-title daa g-blog-title'>${draft}</h1>`
        }
        return "<h1 class='blog-title daa g-blog-title'></h1>"
    }

    const handleTitleChange = (event: any) => {
        text.current = event.target.value;
        if (text.current === "<br>") {
            let bt = document.getElementById("blogTitle");
            let br = bt?.firstChild;
            if (br) {
                bt?.removeChild(br);
            }
            const hchild = document.createElement('h1')
            hchild.className = "blog-title daa g-blog-title";
            bt?.appendChild(hchild)
        }
        else if (event.target.value.indexOf("<div><br></div>") !== -1) {
            let bt = document.getElementById("blogTitle");
            let br = bt?.lastChild;
            if (br) {
                bt?.removeChild(br);
            }
        }
        const bt = document.getElementById('blogTitle')
        sessionStorage.setItem('title', bt?.innerText === undefined ? '' : bt?.innerText)
    }

    const handleTextChange = (event: any) => {
        text.current = event.target.value;

        // if (event.target.value === "<br>") {
        //     let bc = document.getElementById("blogContent")
        //     let br = bc?.firstChild;
        //     if (br) {
        //         bc?.removeChild(br);
        //     }
        // }
        if (event.target.value === ("")) {
            let bc = document.getElementById("blogContent")
            if (bc) {
                bc.innerHTML = '<p class="paragraph dba"></p>'
            }
        }

        // if (event.target.value.indexOf('div') !== -1) {
        //     let bc = document.getElementById("blogContent")
        //     if (bc) {
        //         bc.innerHTML = '<p class="paragraph dba"></p>'
        //     }
        //     const pchild = document.createElement('p');
        //     pchild.className = 'paragraph dba';
        //     const divChild = document.querySelector('.db div')
        //     if (divChild) {
        //         bc?.replaceChild(pchild, divChild);
        //     }
        // }

        const bc = document.getElementById('blogContent')
        sessionStorage.setItem('content', bc?.innerHTML === undefined ? '' : bc?.innerHTML)

    }

    const handlePublish = () => {
        const bt = document.getElementById('blogTitle')
        const bc = document.getElementById('blogContent')
        const title = document.querySelector('.blog-title');
        const content = document.querySelector('.paragraph');

        if (title?.innerHTML === '') {
            bt?.focus();
            return;
        }
        if (content?.innerHTML === '') {
            bc?.focus();
            return;
        }
        setPublish(!publish)
    }


    return (
        <div className="aa editor clearfix">
            <div className="ba editor-container " >
                <div className="cb submit-autosave"><span></span><button className="cbb" onClick={handlePublish}>Publish</button></div>
                <div className="ca editor-content-container">
                    <ContentEditable key="123" tagName="section" id="blogTitle" className="da clearfix" html={initTitle()} onChange={handleTitleChange} />
                    <hr />
                    <ContentEditable key="12" tagName="article" id="blogContent" className="db clearfix g-text" html={initContent()} onChange={handleTextChange} />
                    <EditBar />
                    <AddImgsBar />
                </div>
            </div>
            <div className="publish-block" id="publishBlog" style={{ display: publish ? 'block' : 'none' }}>
                <PublishBlog setPublish={setPublish} />
            </div>
        </div >
    )
}

