import React, { useRef, useState } from 'react'
import './index.scss'
import { ReactComponent as InstagramBlackIcon } from '../../assests/svgs/instagram-black.svg';
import { ReactComponent as LinkedinBlackIcon } from '../../assests/svgs/linkedin-black.svg';
import { ReactComponent as FacebookBlackIcon } from '../../assests/svgs/facebook-black.svg';
import { ReactComponent as GithubBlackIcon } from '../../assests/svgs/github-black.svg';
import { ReactComponent as CodepenBlackIcon } from '../../assests/svgs/codepen-black.svg';
import axios from 'axios';
import BlackCat from '../../components/BlackCat';
export default function Contact() {
    const email = useRef<HTMLInputElement>(null);
    const name = useRef<HTMLInputElement>(null);
    const description = useRef<HTMLTextAreaElement>(null);
    const emailChange = useRef('')
    const nameChange = useRef('')
    const descriptionChange = useRef('')
    const [sent, setSent] = useState(false)
    const handleEmailChange = (e: any) => {
        emailChange.current = e.target.value;
        if (e.target.value.length > 100) {
            emailChange.current = e.target.value.slice(0, 100)
        }
    }
    const handleNameChange = (e: any) => {
        nameChange.current = e.target.value;
        if (e.target.value.length > 100) {
            nameChange.current = e.target.value.slice(0, 100)
        }
    }
    const handleDescriptionChange = (e: any) => {
        descriptionChange.current = e.target.value;
        if (e.target.value.length > 1000) {
            descriptionChange.current = e.target.value.slice(0, 1000)
        }
    }
    const handleSend = (e: any) => {
        if (email.current && name.current && description.current) {
            if (emailChange.current === '') {
                email.current.focus();
                return;
            }
            if (nameChange.current === '') {
                name.current.focus();
                return;
            }
            if (descriptionChange.current === '') {
                description.current.focus();
                return;
            }

            axios.post('/ajax/sendInquiry', {
                params: {
                    email: emailChange.current,
                    name: nameChange.current,
                    description: descriptionChange.current,
                    token: localStorage.getItem('token')
                }
            }).then(res => {
                const { status } = res.data;
                if (status) {
                    setSent(true);
                    if (email.current && name.current && description.current) {
                        email.current.value = '';
                        name.current.value = '';
                        description.current.value = '';
                        descriptionChange.current = '';
                        emailChange.current = '';
                        nameChange.current = '';
                    }
                }
            })
        }

    }
    const handleClose = () => {
        setSent(false)
    }
    return (
        <div className="contact clearfix">
            <div className="contact-pos clearfix">
                <div className="contact-pos-bg clearfix">
                    <div className="contact-img">
                        <BlackCat />
                    </div>
                    <div className="contact-container">
                        <h1>Your opinion matters!</h1>
                        <p>
                            If you have any suggestions or questions about the website, complaints or bugs to report, please kindly fill the form down below. I would be glad to  hear from you  and I will try to get back to you as soon as possible. Thank you very much.

                        </p>
                        <div className='contact-input'>
                            <input ref={email} className="contact-input-email" type="text" onChange={handleEmailChange} placeholder="* Your email address.." />
                            <input ref={name} className="contact-input-name" type="text" onChange={handleNameChange} placeholder="* How should I address you.." />
                            <textarea ref={description} className="contact-input-description" onChange={handleDescriptionChange} name="" id="" cols={30} rows={10} placeholder="Description of the inquiry...(maximum 1000 characters)"></textarea>
                            {/* <input className="contact-input-description" type="text" placeholder="Description of the inquiry.." /> */}
                            <button className="contact-input-send" onClick={handleSend}>Send</button>
                            <div className="contact-input-feedback" style={{ display: sent ? 'flex' : 'none' }}>
                                <div className="contact-input-feedback-container"><span>Your inquiry has been successfully sent! Thank you.</span>
                                    <button onClick={handleClose}>Close</button></div></div>
                        </div>
                        <h1>Get in touch!</h1>
                        <p>Or if you simply just want to connect, please don't hesitate to reach me on my social media!</p>
                        <div className="contact-socialmedia">
                            <a href="https://www.instagram.com/zhangpeng42/" className="contact-socialmedia-icon1"><InstagramBlackIcon />1</a>
                            <a href="https://www.facebook.com/tillian.roky/" className="contact-socialmedia-icon2"><FacebookBlackIcon />2</a>
                            <a href="https://www.linkedin.com/in/peng-zhang-55a653113/" className="contact-socialmedia-icon3"><LinkedinBlackIcon />3</a>
                            <a href="https://github.com/ZhangPeng4242" className="contact-socialmedia-icon4"><GithubBlackIcon />4</a>
                            <a href="https://codepen.io/zhangpeng4242" className="contact-socialmedia-icon5"><CodepenBlackIcon />5</a>
                        </div>
                        <p>Or directly via my email: zhangpeng4242@gmail.com</p>
                    </div>
                </div>
            </div>
        </div>
    )
}
