import React, { useEffect, useState, useRef } from 'react'
import { useHistory } from 'react-router';
import './index.scss'
import axios from 'axios';
import { ReactComponent as CloseIcon } from '../../assests/svgs/close-black.svg'
export default function PublishBlog(props) {
    const { setPublish } = props;
    const main_img_url = useRef('');
    const abstract = useRef('')
    const tagCount = useRef(0)
    const tags = useRef([])
    const tagsInput = useRef(null)
    const imgInput = useRef(null)
    const container1 = useRef(null)
    const container2 = useRef(null)
    const history = useHistory()
    const [oversize, setOversize] = useState(undefined)
    const [publishing, setPublishing] = useState(false)
    const handleUpload = () => {
        const img = document.getElementById('img-frontpage');
        const imgfile = img.files[0];
        const token = localStorage.getItem('token')

        if (!imgfile) {
            console.log(13123);
            return;
        }
        if (imgfile.size > 1048576) {
            console.log("picture oversize");
            setOversize(true);
            return
        }
        setOversize(false);
        let param = new FormData();
        param.append('img', imgfile);

        axios({
            method: 'post',
            url: `/ajax/imgupload?token=${token}`,
            data: param,
            headers: { "Content-Type": "multipart/form-data image/png" },
        }).then(res => {
            const { status, img_url, img_name } = res.data;
            if (status) {
                console.log(img_url)
                main_img_url.current = img_url;
            } else {
                setPublishing('failed')
            }
        })


    }
    const handleAbstractChange = (e) => {
        console.log(e.target.value.length)
        if (e.target.value.length <= 300) {
            abstract.current = e.target.value;
        } else {
            e.target.value = e.target.value.slice(0, 300)
        }
    }

    const handleSubmit = () => {
        const blogTitle = sessionStorage.getItem('title');
        const blogContent = sessionStorage.getItem('content');
        const token = localStorage.getItem('token');
        console.log(main_img_url.current)
        if (tags.current.length === 0) {
            tagsInput.current.focus();
            return;
        }
        if (oversize !== false) {
            imgInput.current.focus();
            return;
        }
        setPublishing(true);
        axios.post('/ajax/publish-blog', {
            params: {
                blogTitle,
                blogContent,
                authorToken: token,
                abstract: abstract.current,
                main_img_url: main_img_url.current,
                tags: tags.current
            }
        }).then(res => {
            const { status } = res.data;
            if (status) {
                history.push('/publish-feedback')
                sessionStorage.removeItem('title');
                sessionStorage.removeItem('content');
            }
        })

    }


    const handleTags = (e) => {
        if (tagCount.current >= 5) {
            return;
        }
        if (e.keyCode === 13 && e.target.value !== '') {

            const ul = document.getElementById('publishTags')

            const li = document.createElement('li')
            const span1 = document.createElement('span')
            const span2 = document.createElement('span')
            const tag = e.target.value.trim().toUpperCase();
            span1.innerText = tag;
            span2.innerText = 'x';
            span2.onclick = () => {
                ul.removeChild(li)
                tagCount.current -= 1
            }
            li.appendChild(span1)
            li.appendChild(span2)
            ul.appendChild(li);
            tags.current.push(tag)
            tagCount.current += 1
            e.target.value = '';
        }
    }
    const handleClose = () => {
        setPublish(false)
    }
    const handleOutterClose = (e) => {
        if (e.target === container1.current || e.target === container2.current) {
            setPublish(false)
        }
    }

    return (
        <div className="publish-blog" ref={container1} onClick={handleOutterClose}>
            <div className="ba" ref={container2}>
                <div className="publish-blog-container">
                    <CloseIcon className="icon-close" onClick={handleClose} />
                    <h1>Publish Your Blog</h1>
                    <section className="publish-blog-title">Title: {sessionStorage.getItem('title')}</section>
                    <section className="publish-blog-tags">
                        <span className="publish-blog-tags-text">Tags:</span>
                        <div><ul id='publishTags' className="clearfix"></ul></div> <input type="text" placeholder="*Press Enter to add tags" onKeyDown={handleTags} ref={tagsInput} /></section>
                    <section className="publish-blog-abstract"><textarea name="" id="abstract" cols={60} rows={5} placeholder="(Optional) Please write a short abstract about this blog, limited to 300 characters." onChange={handleAbstractChange}></textarea></section>
                    <section className="publish-blog-uploadimg">
                        <span>Profile image: </span><input placeholder="hh" type="file" id="img-frontpage" accept="image/png, image/jepg, image/jpg" onChange={handleUpload} ref={imgInput} />
                        <div className="publish-blog-err" style={{ display: oversize === true ? "block" : "none" }}>
                            <span className="publish-blog-err-msg" >* Maximum image size 1mb</span>
                        </div>
                    </section>

                    <section className="publish-blog-submit"><button onClick={handleSubmit}>Submit</button>
                        <span style={{ display: publishing === true ? 'block' : 'none' }}>Publishing...</span>
                        <span style={{ display: publishing === 'failed' ? 'block' : 'none' }}>publish failed please try again later...</span>
                    </section>
                </div>
            </div>
        </div>
    )
}
