import React, { useState, useRef } from 'react'
import { useHistory, useLocation } from 'react-router';
import './index.scss'
import SignupWithEmail from '../../../pages/SignUpWithEmail';
import LoginWithEmail from '../../../pages/LoginWithEmail';
import { divMode } from 'tsparticles';
import { Route } from 'react-router';
import { ReactComponent as WriteIcon } from '../../../assests/svgs/write.svg'

export default function UserBlock(props: any) {
    const { loggedIn, setLoggedIn } = props
    const [signup, setSignup] = useState<Boolean>(false);
    const [login, setLogin] = useState<void | Boolean>(false)
    const [dropdown, setDropdown] = useState<Boolean>(false)
    const section = useRef<HTMLDivElement>(null)
    const history = useHistory()
    const location = useLocation();
    const handleSignup = () => {
        setSignup(true);
    }
    const handleLogin = () => {
        setLogin(true);
    }
    const handleExit = (event: any) => {
        if (event.target === section.current) {
            if (signup) { setSignup(false) };
            if (login) { setLogin(false) };
        }
    }
    const handleLogOut = () => {
        localStorage.clear();
        sessionStorage.clear();
        setDropdown(!dropdown);
        setLoggedIn(!loggedIn);
        history.push('/home?redirect=true');
    }

    const handleAvatarClick = () => {
        setDropdown(!dropdown)
    }
    const handleMyblogs = () => {
        history.push('/myblogs');
        setDropdown(false);
    }
    let avatar = sessionStorage.getItem('avatar_url') !== null ? sessionStorage.getItem('avatar_url') : undefined
    const avatar_url: string | undefined = avatar === null ? '' : avatar;
    return loggedIn ? (
        <div className="userblock-loggedin">
            <section className="ebab topbar-write">
                {loggedIn ? <a href="/drafts"><WriteIcon className='icon-write' /> </a> : ''}
            </section>
            <div><span>{sessionStorage.getItem('username')}</span></div>
            <div><img src={avatar_url} alt="" className="ebacb topbar-login-avatar" onClick={handleAvatarClick} />
                <div className="avatar-dropdown" style={{ display: dropdown ? 'block' : 'none' }}><ul><li onClick={handleMyblogs}>My Blogs</li><li onClick={handleLogOut}>Log Out</li></ul></div></div>
        </div>
    ) : (
        <div>
            <div className="userblock">
                <span className="ebaca" onClick={handleLogin}>Login </span>
                <span className="ebacb" onClick={handleSignup}>Join us</span>
            </div>
            <section ref={section} className="popup-block" style={{ display: signup || login ? 'block' : 'none' }} onClick={handleExit}>
                <div className="aa">

                    {signup ? <SignupWithEmail setSignup={setSignup} /> : ''}
                    {login ? < LoginWithEmail setLogin={setLogin} /> : ''}
                </div>
            </section>
        </div>
    )
}

