import axios from 'axios';
import React, { useState, useEffect, useRef } from 'react'
import { useHistory } from 'react-router';
import './index.scss'
import OAuthLogin from '../../components/OAuthLogin';
import WelcomeHeader from '../../components/WelcomeHeader';
import { ReactComponent as CloseIcon } from '../../assests/svgs/close.svg'

export default function SignupWithEmail(props: any) {
    const { setSignup } = props;
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [passwordConf, setPasswordConf] = useState('');
    const usernameFormat = /^[a-zA-Z0-9_-]{4,16}$/;
    const emailFormat = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
    const passwordFormat = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/;

    const [fmtCheckUsername, setFmtCheckUsername] = useState<void | Boolean>(undefined);
    const [fmtCheckEmail, setFmtCheckEmail] = useState<void | Boolean>(undefined);
    const [fmtCheckPassword, setFmtCheckPassword] = useState<void | Boolean>(undefined);
    const [fmtCheckConfPassword, setFmtCheckConfPassword] = useState<void | Boolean>(undefined);

    const [usernameNotExisted, setUsernameNotExisted] = useState<void | Boolean>(undefined);
    const [emailNotExisted, setEmailNotExisted] = useState<void | Boolean>(undefined);

    const usernameRef = useRef<HTMLInputElement>(null), emailRef = useRef<HTMLInputElement>(null), passwordRef = useRef<HTMLInputElement>(null), passwordConfRef = useRef<HTMLInputElement>(null);
    const privacyPolicy = useRef<HTMLInputElement>(null)
    const history = useHistory();
    const handleChange = async (event: any) => {
        const { value } = event.target;
        switch (event.target.id) {
            case 'username':
                setUsername(value);
                break;
            case 'email':
                setEmail(value);

                break;
            case 'password1':
                setPassword(value);

                break;
            case 'password2':
                setPasswordConf(value)

                break;
            default:
                break;
        }
    }
    //useState Hooks are asynchronous, therefore we need to use useEffect Hook
    useEffect(() => setFmtCheckUsername(usernameFormat.test(username)), [username])
    useEffect(() => setFmtCheckEmail(emailFormat.test(email)), [email])
    useEffect(() => setFmtCheckPassword(passwordFormat.test(password)), [password])
    useEffect(() => { if (password !== '') setFmtCheckConfPassword(passwordConf === password); }, [passwordConf])

    // useEffect(() => { if (setUsernameNotExisted !== undefined) setUsernameNotExisted(undefined); }, [fmtCheckUsername])
    // useEffect(() => { if (setEmailNotExisted !== undefined) setEmailNotExisted(undefined); }, [fmtCheckEmail])

    //check if email or username have already been registered
    const handleBlur = (event: any) => {
        const { value } = event.target
        switch (event.target.id) {
            case 'username':
                if (usernameFormat) {
                    axios.get('/ajax/user-existed-check', { params: { username: value } }).then(res => {
                        setUsernameNotExisted(!res.data.status)
                    })
                }
                break;
            case 'email':
                if (emailFormat) {
                    axios.get('/ajax/user-existed-check', { params: { email: value } }).then(res => {
                        setEmailNotExisted(!res.data.status);
                    })
                }
                break;

            default:
                break;
        }
    }


    const handleSignUp = () => {

        //only when all the formats are correct also username and email not existed can we create a new account
        if (fmtCheckUsername && fmtCheckEmail && fmtCheckPassword && fmtCheckConfPassword && usernameNotExisted && emailNotExisted && privacyPolicy.current?.checked) {
            console.log('req....')
            axios.post('/ajax/emailsignup', {
                params: {
                    username, email, password
                }
            }).then(res => {
                const { redirect_uri } = res.data;
                setSignup(false);
                history.push(redirect_uri);

            })
        } else {
            if (!fmtCheckUsername) { usernameRef.current?.focus(); return };
            if (!fmtCheckEmail) { emailRef.current?.focus(); return };
            if (!fmtCheckPassword) { passwordRef.current?.focus(); return };
            if (!fmtCheckConfPassword) { passwordConfRef.current?.focus(); return };
            if (!privacyPolicy.current?.checked) {
                const privacy = document.getElementById('privacy')
                if (privacy) {
                    privacy.style.color = 'red';
                    setTimeout(() => {
                        privacy.style.color = 'rgba(117,117,117,1)';
                    }, 1000)
                }
            }
        }
    }

    const handleClose = () => {
        setSignup(false);
    }
    return (
        <div className="signup-with-email">
            <div className="aa signup-container clearfix">
                <CloseIcon className='icon-close' onClick={handleClose} />
                <WelcomeHeader />
                <OAuthLogin />
                <div className="signup-input">
                    <section>
                        <input ref={usernameRef} type="text" name="username" id="username" placeholder="Username" onChange={handleChange} onBlur={handleBlur} />
                        <div></div>
                        <span style={{ color: fmtCheckUsername && usernameNotExisted ? 'green' : 'red' }}>
                            {fmtCheckUsername !== undefined ?
                                (fmtCheckUsername ?
                                    (usernameNotExisted !== undefined ?
                                        (usernameNotExisted ? '√ Username is valid' : '*Username already exists!'
                                        ) : ''
                                    ) : '* At least 4 characters, symbols not allowed')
                                : ''}
                        </span>
                    </section>
                    <section>
                        <input ref={emailRef} type="text" name="email" id="email" placeholder="Email" onChange={handleChange} onBlur={handleBlur} />
                        <div></div>
                        <span style={{ color: fmtCheckEmail && emailNotExisted ? 'green' : 'red' }}>
                            {fmtCheckEmail !== undefined ?
                                (fmtCheckEmail ?
                                    (emailNotExisted !== undefined ?
                                        (emailNotExisted ? '√ Email is valid' : '* Email already registered!'
                                        ) : ''
                                    ) : '* Please enter correct email address')
                                : ''}
                        </span>
                    </section>
                    <section>
                        <input ref={passwordRef} type="password" name="password1" id="password1" placeholder="Password" onChange={handleChange} />
                        <span style={{ color: fmtCheckPassword ? 'green' : 'red' }}>
                            {fmtCheckPassword ? '√ Password is valid' : '* At least four characters with one alphabet and one number.'}</span>
                    </section>
                    <section>
                        <input ref={passwordConfRef} type="password" name="password2" id="password2" placeholder="Confirm password" onChange={handleChange} />
                        <span style={{ color: fmtCheckConfPassword ? 'green' : 'red' }}>
                            {fmtCheckConfPassword !== undefined ?
                                (fmtCheckConfPassword ? '√ Password confirmed!' : '*Password is not the same!') : ''}</span>
                    </section>
                    <button onClick={handleSignUp}>SIGN UP</button>
                    <div className="signup-privacy-policy"><input type="checkbox" title="checkbox-privacy-policy" ref={privacyPolicy} /><span id="privacy" >I acknowledge that I have read and agree to the <a href={process.env.PUBLIC_URL + '/assets/privacy-policy/privacy-policy.html'}>privacy policy</a>.</span></div>
                </div>
            </div>
        </div>
    )
}
