import React, { useEffect, useLayoutEffect, useState } from 'react'
import { useHistory, useLocation } from 'react-router'
import './index.scss'
import UserBlock from './UserBlock'
import { loginCheck } from '../../functionutils'
import CatLogo from '../CatLogo'

export default function Topbar() {
    const [loggedIn, setLoggedIn] = useState<undefined | Boolean>(undefined);
    const [foldDropDown, setFoldDropdown] = useState(false)
    const location = useLocation();
    const [nav, setNav] = useState('');
    const history = useHistory();
    const [, updateState] = useState({});
    const forceUpdate = React.useCallback(() => updateState({}), []);
    // console.log(location)
    useEffect(() => {
        const loggedin = loginCheck();
        if (loggedin) {
            setLoggedIn(true);
        } else {
            setLoggedIn(false);
        }
        if (location.pathname.indexOf('redirect-page') !== -1) {
            setLoggedIn(true);
        }
    })
    useEffect(() => {
        switch (location.pathname) {
            case '/aboutme':
                setNav('me')
                break;
            case '/bloglist':
                setNav('blogs')
                break;
            case '/tools':
                setNav('tools')
                break;
            case '/contact':
                setNav('contact')
                break;
            default:
                break;
        }
    })

    const handleUnfold = () => {
        setFoldDropdown(!foldDropDown)
    }

    return (
        <div className="aa topbar">
            <div className="ba">
                <div className="ca">
                    <div className="da clearfix">
                        <section className="ea topbar-title clearfix">
                            <a href="/home" className="eaa g-title">Pengzstack</a><CatLogo />
                        </section>
                        <div className="eb">
                            <div className="eba">
                                <section className="ebac topbar-login g-subtext">
                                    <UserBlock loggedIn={loggedIn} setLoggedIn={setLoggedIn} />
                                </section>
                                <section className="ebaa topbar-nav">
                                    <a href="/aboutme" className="ebaaa">ME <span style={{ backgroundColor: nav === 'me' ? '#222222' : '#fff' }}></span></a>
                                    <a href="/bloglist" className="ebaaa">BLOGS <span style={{ backgroundColor: nav === 'blogs' ? '#222222' : '#fff' }}></span></a>
                                    <a href="/tools" className="ebaaa">TOOLS  <span style={{ backgroundColor: nav === 'tools' ? '#222222' : '#fff' }}></span></a>
                                    <a href="/contact" className="ebaaa">CONTACT <span style={{ backgroundColor: nav === 'contact' ? '#222222' : '#fff' }}></span></a></section>
                                <section className="ebad topbar-nav-fold" onClick={handleUnfold}>
                                    <div className="topbar-nav-fold-dashes">
                                        <span className="ebada dash"></span>
                                        <span className="ebada dash"></span>
                                        <span className="ebada dash"></span>
                                    </div>
                                    <div className="topbar-nav-fold-dropdown" style={{ display: foldDropDown ? 'block' : 'none' }}>
                                        <ul>
                                            <li> <a href="/aboutme" className="ebaaa">ME</a></li>
                                            <li>  <a href="/bloglist" className="ebaaa">BLOGS</a></li>
                                            <li>  <a href="/tools" className="ebaaa">TOOLS</a></li>
                                            <li> <a href="/contact" className="ebaaa">CONTACT</a></li>
                                        </ul>
                                    </div>
                                </section>
                            </div>

                        </div>
                    </div>
                </div>
            </div >
        </div >
    )
}

