import React, { useEffect, useRef, useState } from 'react'
import axios from 'axios'
import BlinkAnimation from '../../components/BlinkAnimation';
import './index.scss'

import { fmtstr } from '../../functionutils';
const frontEnd = ['<HTML/>', 'Css(Sass)', 'Javascript', 'Typescript', "React"];
const backEnd = ['Linux', 'Nodejs', 'GO', 'Python', 'Nginx', 'MongoDB', 'MySql']
const others = ['Git', 'WebPack', ' Protocols(TCP/IP, HTTP…)']
const updateFeatures = ['User personal page', ' Comments section', 'Like function', 'Search blog function', 'Filter blog function', ' Blog list UI iteration', ' Blog recommendation algorithms']
export default function Home2() {
    const [recommendBlogs, setRecom] = useState()
    const [recentBlogs, setRecent] = useState()
    const pageMounted = 1;
    useEffect(() => {
        axios.get('/ajax/homepage-getblogs').then(res => {
            const { status } = res.data;
            if (status) {
                const { recommend } = res.data;
                const { newBlogs } = res.data;
                let recom = [];
                let recent = [];
                recommend.map((item) => {
                    const { title, username, avatar_url, publish_date, tags, blog_id } = item;
                    recom.push({ title, username, avatar_url, publish_date, tags, blog_id })
                })
                newBlogs.map((item) => {
                    const { title, username, avatar_url, publish_date, tags, blog_id } = item;
                    recent.push({ title, username, avatar_url, publish_date, tags, blog_id })
                })
                setRecom(recom)
                setRecent(recent)
            }
            console.log(recommendBlogs)
        })
    }, [pageMounted])
    return (
        <div className="home clearfix">
            <div className="ba">
                <section className="ca container-top" >
                    <div className="home-animation"> < BlinkAnimation /></div>
                </section>
                <section className="ca container-left">

                    {/* <span className="animation-switch">click the animation to pause/start</span> */}
                    <div className="home-introduction"><p>Welcome! Pengzstack is a website primarily dedicated to improving skills in Full-stack development, featuring on sharing pragmatic and up-to-date knowledge, as well as solving real issues.  The subjects covered in this website are mostly(but not limited to):</p>
                        <p>Front-end:{frontEnd.map((item, index) => {
                            return <span key={index}>{item}</span>
                        })}
                        </p>
                        <p>Back-end:
                            {backEnd.map((item, index) => {
                                return <span key={index}>{item}</span>
                            })}</p>
                        <p>Others:
                            {others.map((item, index) => {
                                return <span key={index}>{item}</span>
                            })}</p>
                        <p>
                            I built this website so as to practice my own skills as well as to keep track and share what I have learned. </p>
                        <hr />
                        <p>Pengzstack is constantly under development, the current version is 1.1, more features will be added in the future versions.</p>
                        <p>Current features on the list to be developed:</p>
                        <ul>{updateFeatures.map((item, index) => {
                            return <li key={index}>{item}</li>
                        })
                        }</ul>
                        <p>I apologize if the current user experience on this website is minimalistic(or rather simplistic), or if you have encountered any bugs while browsing. I would be very happy to hear from you if you have any suggestions, complaints or bugs to report. Please kindly send your inquiry through <a href="/contact">contact</a> page</p>
                    </div>
                </section>

                <section className="container-right">
                    <div className="recomended-blogs"><h1>Recommended for you</h1>
                        <div>
                            <ul>{recommendBlogs !== undefined ? recommendBlogs.map(item => {
                                return <li key={item.blog_id}>
                                    <a href={`/bloglist/blog/${fmtstr(item.title, item.blog_id)}`}>{item.title}</a>
                                    <div><img src={item.avatar_url} alt="" />
                                        <span>{item.username}</span>
                                        <span>{new Date(item.publish_date).toDateString().slice(3)}</span>
                                        <span className="homepage-right-tags">{item.tags !== 0 ? item.tags[0] : 'BLOG'}</span>
                                    </div>
                                </li>;
                            }) : ''}</ul>
                        </div></div>
                    <div className="newest-blogs"><h1>Most recent blogs</h1>
                        <div>
                            <ul>{recentBlogs !== undefined ? recentBlogs.map(item => {
                                return <li key={item.blog_id}>
                                    <a href={`/bloglist/blog/${fmtstr(item.title, item.blog_id)}`}>{item.title}</a>
                                    <div><img src={item.avatar_url} alt="" />
                                        <span>{item.username}</span>
                                        <span>{new Date(item.publish_date).toDateString().slice(3)}</span>
                                        <span className="homepage-right-tags">{item.tags.length !== 0 ? item.tags[0] : 'BLOG'}</span>
                                    </div>
                                </li>;
                            }) : ''}</ul>



                        </div>
                    </div>
                </section>
            </div>
        </div>
    )
}
